<template>
  <div>
    <h3 class="page-head">
      <router-link to='/admin/document'><i class="el-icon-arrow-left"></i></router-link>/
    </h3>
    <div class="doc-title">
      <p><span>{{ details.name }}</span></p>
      <router-link :to="{path:'chapter/'+ details.id}" class="el-button el-button--primary"></router-link>
    </div>
    <div class="content">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="first">
          <div class="tab-content-baseInfo">
            <table>
              <tr>
                <td class="title"></td>
                <td>{{ details.name }}</td>
                <td><el-button type="text" @click="modalShow('name')"></el-button></td>
              </tr>
              <tr>
                <td class="title"></td>
                <td>{{ details.description }}</td>
                <td><el-button type="text" @click="modalShow('description')"></el-button></td>
              </tr>
            </table>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second" v-if="manageVisible">
          <div class="tab-content-manage">
            <el-table class="w7-table" :data="docuserList" ref="docuserTable" style="width: 100%">
              <el-table-column prop="username" label=""></el-table-column>
              <el-table-column prop="has_creator_name" label=""></el-table-column>
              <el-table-column align="right">
                <template slot="header">
                  <el-button type="text" @click="dialogAddManageVisible = true"></el-button>
                </template>
                <template slot-scope="scope">
                  <el-button type="text" @click="removeManage(scope.row.id)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <!--  -->
    <el-dialog class="w7-dialog" title="" :visible.sync="dialogDocInfoVisible" :close-on-click-modal="false" center>
      <el-form :model="details" :label-width="formLabelWidth">
        <el-form-item label="" v-if="docInfoVisible == 'name'">
          <el-input v-model="name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" v-if="docInfoVisible == 'description'">
          <el-input type="textarea" :rows="2" v-model="description"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="updateDocument"> </el-button>
        <el-button @click="dialogDocInfoVisible = false"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog class="w7-dialog w7-dialog-user" title="" :visible.sync="dialogAddManageVisible" :close-on-click-modal="false" center>
      <!-- <el-form :model="userInfo" :label-width="formLabelWidth">
        <el-form-item label="">
          <el-input v-model="userInfo.username" autocomplete="off"></el-input>
        </el-form-item>
      </el-form> -->
      <div class="demo-input-suffix">
        <!-- <span></span> -->
        <el-input placeholder="" v-model="keyword" @keyup.enter.native="getuserlist">
          <i slot="suffix" class="el-input__icon el-icon-search" @click="getuserlist"></i>
        </el-input>
      </div>
      <el-table class="w7-table-small" height="250"
        :data="userList"
        ref="multipleTable"
        :header-cell-style="{background:'#f7f9fc',color:'#606266'}"
        @row-click="rowClick">
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="created_at" label=""></el-table-column>
      </el-table>
      <div class="w7-pagination">
        <el-pagination class="fr"
          background
          :hide-on-single-page="true"
          @current-change="getuserlist"
          layout="prev, pager, next, total"
          :current-page.sync="currentPageUser"
          :page-count="pageCountUser"
        >
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <!-- <el-button type="primary" @click="addManage"> </el-button> -->
        <el-button @click="dialogAddManageVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'manageSetting',
  data() {
    return {
      id: this.$route.params.id,//id
      manageVisible: false,//
      activeName: 'first',//tabs
      details: '',//
      name: '',//
      description: '',//
      dialogDocInfoVisible: false,//
      formLabelWidth: '90px',
      docInfoVisible: '',//
      docuserList: [],//
      dialogAddManageVisible: false,//
      keyword: '',//
      userList: [],//
      pageCountUser: 0,//
      currentPageUser: 1//
    }
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    modalShow(labelname) {
      this.docInfoVisible = labelname
      this.dialogDocInfoVisible = true
      if (labelname == 'name') {
        this.name = this.details.name
      } else {
        this.description = this.details.description
      }
    },
    getdetails() {
      this.$post('/admin/document/getdetails',{
        id: this.id
      })
        .then(res => {
          this.details = res
          this.manageVisible = res.has_creator != 3 ? true : false
          if(this.manageVisible) {
            this.getdocuserlist()
          }
        })
    },
    getdocuserlist() {
      this.$post('/admin/document/getdocuserlist',{
        id: this.id
      })
        .then(res => {
          this.docuserList = res
        })
    },
    updateDocument() {
      this.$post('/admin/document/update',{
        id: this.details.id,
        name: this.name ? this.name : this.details.name,
        description: this.description ? this.description : this.details.description
      })
        .then(() => {
          this.$message('')
          this.details.name = this.name ? this.name : this.details.name
          this.details.description = this.description ? this.description : this.details.description
          this.dialogDocInfoVisible = false
          this.name = ''
          this.description = ''
        })
    },
    getuserlist() {
      this.$post('/admin/user/getuserlist',{
         username: this.keyword,
         page: this.currentPageUser
      })
        .then(res => {
          this.userList = res.data
          this.pageCountUser = res.pageCount
        })
    },
    removeManage(id) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$post('/admin/auth/leave_document',{
          user_id: id,
          document_id: this.id
        })
          .then(() => {
            this.getdocuserlist()
            this.$message('')
          })
      })
    },
    addManage(userId) {
      this.$post('/admin/auth/invite_user',{
        user_id: userId,
        document_id: this.id
      })
        .then(() => {
          this.getdocuserlist()
          this.$message('')
          this.dialogAddManageVisible = false
        })
    },
    rowClick(row) {
      this.addManage(row.id)
    }
  },
  created() {
    this.getdetails()
    // this.getuserlist()
  }
}
</script>

