<template>
  <div>
    <h3 class="page-head">
      
    </h3>
    <div class="search-box">
      <div class="demo-input-suffix">
        <el-input placeholder="" v-model="keyword" @keyup.enter.native="searchDoc">
          <i slot="suffix" class="el-input__icon el-icon-search" @click="searchDoc"></i>
        </el-input>
      </div>
      <div class="demo-input-btn">
        <el-button type="primary" @click="dialogDocInfoVisible = true"></el-button>
      </div>
    </div>
    <el-table class="w7-table" :data="docList" empty-text=""
        v-loading="loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        :header-cell-style="{background:'#f7f9fc',color:'#606266'}">
      <el-table-column label="">
        <template slot-scope="scope">
          <i class="w7-icon-fileFolder"></i>
          <span style="margin-left: 10px">{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="username"></el-table-column>
      <!-- <el-table-column label="" prop=""></el-table-column> -->
      <el-table-column label="" prop="created_at"></el-table-column>
      <el-table-column label="" align="right">
        <template slot-scope="scope">
          <el-button type="text" v-if="scope.row.has_creator != 3 || UserInfo.has_privilege == 1" @click="removeDoc(scope.row.id)"></el-button>
          <router-link
            :to="{path: 'document/chapter/' + scope.row.id}"
            class="el-button el-button--text"></router-link>
          <router-link
            :to="{path: 'document/'+ scope.row.id}"
            class="el-button el-button--text" v-if="scope.row.has_creator != 3 || UserInfo.has_privilege == 1">
            
          </router-link>
          <el-button type="text" v-if="scope.row.has_creator != 3 || UserInfo.has_privilege == 1"
            :class="{redBtn: scope.row.is_show == 1}"
            @click="updateDoc(scope.row.id, scope.row.is_show)">{{scope.row.is_show == 2 ? "" : ""}}</el-button>
          <el-button type="text" :class="{'is-disabled': scope.row.is_show == 2}" @click="readDoc(scope.row.id, scope.row.is_show)"></el-button>
        </template>
      </el-table-column>
      <div class="nodata" slot="empty">
        <p><el-button type="text" @click="dialogDocInfoVisible = true"></el-button></p>
      </div>
    </el-table>
    <el-pagination
      background
      @current-change = "getList"
      layout="prev, pager, next, total"
      prev-text=""
      next-text=""
      :current-page.sync = "currentPage"
      :page-count="pageCount"
      :hide-on-single-page = "true"
    >
    </el-pagination>
    <!--  -->
    <el-dialog class="w7-dialog" title="" :visible.sync="dialogDocInfoVisible" :close-on-click-modal="false" center>
      <el-form :label-width="formLabelWidth">
        <el-form-item label="">
          <el-input v-model="name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" :rows="2" v-model="description"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createDoc"> </el-button>
        <el-button @click="dialogDocInfoVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: 'docIndex',
  data() {
    return {
      loading: false,
      keyword: '',
      docList: [],
      currentPage: 0,//
      pageCount: 0,//
      total: 0,//
      name: '',
      description: '',
      dialogDocInfoVisible: false,//
      formLabelWidth: '120px'
    }
  },
  methods: {
    getList() {
      this.loading = true
      this.$post('/admin/document/getlist',{
        page: this.currentPage,
        name: this.keyword
      })
        .then(res => {
          this.docList = res.data
          this.pageCount = res.pageCount
          this.total = res.total
          this.loading = false
        })
    },
    searchDoc() {
      this.loading = true
      this.$post('/admin/document/getlist',{
        name: this.keyword
      })
        .then(res => {
          this.docList = res.data
          this.pageCount = res.pageCount
          this.total = res.total
          this.loading = false
        })
    },
    createDoc() {
      this.$post('/admin/document/create', {
        name: this.name,
        description: this.description
      })
        .then(() => {
          this.$message('')
          this.dialogDocInfoVisible = false
          this.getList()
        })
    },
    removeDoc(id) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$post('/admin/document/delete',{
          id: id
        })
          .then(() => {
            this.getList()
            this.$message('')
          })
      })
    },
    updateDoc(id, isShow) {
      this.$post('/admin/document/update',{
          id: id,
          is_show: isShow == 1 ? 2 : 1
        })
          .then(() => {
            //docList
            this.docList.forEach(row => {
              if(row.id == id) {
                row.is_show = isShow == 1 ? 2 : 1
                return
              }
            });
          })
    },
    readDoc(id, isShow) {
      if(isShow == 2) {
        this.$message('')
        return
      }
      let routeUrl = this.$router.resolve({
        path: "/"+ id
      })
      window.open(routeUrl.href, '_blank')
    }
  },
  computed: {
      ...mapGetters({UserInfo: 'UserInfo'})
  },
  created() {
    this.$store.dispatch('getUserInfo')
    this.getList()
  }
}
</script>
<style lang="scss" scoped>
.nodata {
  min-height: 400px;
  background-color: #fff;
  background-image:  url('~@/assets/img/nodata-bg.png');
  background-position:  center;
  background-repeat: no-repeat;
  p {
    padding-top: 280px;
    font-family: MicrosoftYaHei;
    font-size: 14px;
  }
}
.redBtn {
  color: #eb2e56;
}
.w7-icon-fileFolder:after {
  content:url('~@/assets/img/fileFolder-small.png')
}
</style>